

































































































import Localization from "../widget/app-localization.vue";

import { defineComponent, reactive, ref } from "@vue/composition-api";
import Axios from "axios";

export default defineComponent({
  name: "Register",
  components: {
    Localization
  },

  setup() {
    const model = reactive({
      contactEmail: "",
      password: "",
      contactName: "",
      address: "",
      confirm: "",
      registrationPhone: "",
      key: "",
      select: null,
      sex: ["Male", "Female", "Not Provided"],
      age: ["Below 10", "10-20", "21-30", "31-40", "41-50", "51-60", "Above 60"]
    });

    const loading = ref(false);

    function login() {
      if (this.$route.query.key) {
        model.key = this.$route.query.key;
      }
      console.log(model);
      loading.value = true;
      setTimeout(() => {
        this.$router.push("/home");
      }, 1000);
    }

    async function register() {
      await Axios({
        url: "/api/referloauth/signup",
        method: "POST",
        data: model
      })
        .then(async (response) => {
          await this.$store.dispatch("LoginByPhone", {
            phone: model.registrationPhone,
            password: model.password
          });
          await this.$router.push(this.$route.query.redirect || "/");
        })
        .catch((error) => {
          window.alert(error.response["data"]);
          console.log(error.response["data"]);
        });
    }

    return {
      model,
      loading,
      register,
      login
    };
  }
});
